const Product = (props,i)=><div className="item col-xs-12 col-sm-6" >
  <div className="item_image">
    <a href={props.link}><img src={props.img} alt={props.title} /></a>
  </div>
  <div className="item_title">
    <h4><a href={props.link}>{props.title}</a></h4>
  </div>
</div>

export default ({settings})=>{
    const leftProduct = () => {
            const arr = []
            for (let i = 1; i < 4; i++) {
                const show = settings[`xmas_001__featured__left__${i}__show`]
                const title = settings[`xmas_001__featured__left__${i}__name`]
                const img = settings[`xmas_001__featured__left__${i}__img_url`]
                const link = settings[`xmas_001__featured__left__${i}__url`]

                if(show){
                    arr.push(<Product title={title}  img={img} link={link} key={i}></Product>)
                }
           
            }
            return arr;
    }
    const rightProduct = () => {
        const arr = []
        for (let i = 1; i < 4; i++) {
            const show = settings[`xmas_001__featured__right__${i}__show`]
            const title = settings[`xmas_001__featured__right__${i}__name`]
            const img = settings[`xmas_001__featured__right__${i}__img_url`]
            const link = settings[`xmas_001__featured__right__${i}__url`]

            if(show){
                arr.push(<Product title={title}  img={img} link={link} key={i}></Product>)
            }
       
        }
        return arr;
}
    return <section id='block1' data-setting="featured">
    <h2 className="title_background">
  <span>{settings.xmas_001__featured__title}</span>
  <img src={settings.xmas_001__featured__img_url} data-setting="featured__img_url" />
</h2>

     <div className='hot_products'>
        <div className='row'>
            <div className='col-xs-12 col-md-6'>
                <div className='item_wrapper'>
                    <h3>{settings.xmas_001__featured__left__title}</h3>
                    <div className='row'>
                        {leftProduct()}
                    </div>
                </div>
            </div>
            <div className='col-xs-12 col-md-6'>
                <div className='item_wrapper'>
                    <h3>{settings.xmas_001__featured__right__title}</h3>
                    <div className='row'>
                        {rightProduct()}
                    </div>
                </div>
            </div>
        </div>
    </div> 
</section>
}